import { makeStyles } from '@material-ui/core/styles';
import { pxToRem } from '../../../../styles/themes/vad/constants';

const useStyles = makeStyles(theme => ({
  root: {
    color: theme.palette.common.black,
    textDecoration: 'none',
    '& .card-description': {
      padding: theme.spacing(2),
      [theme.breakpoints.up('lg')]: {
        padding: theme.spacing(3),
      },
    },
    '& .listing-card .card-description': {
      '& :not(.img-to-animate-wrapper) img, & .img-to-animate-wrapper img, img': {
        transition: 'none',
        transform: 'scale(1)',
      },
      '& .rating-img': {
        width: 'auto',
        maxHeight: pxToRem(13),
      },
    },
    '& .search-highlight': {
      backgroundColor: theme.palette.common.Sunrise,
      color: theme.mixins.Black(),
    },
  },
  cardWrapper: {
    display: 'flex',
    flexDirection: 'column',
    transition: 'box-shadow .3s, background-color .3s',
    borderBottom: props => !props.cardType && theme.borders.divider,
    height: '100%',
    '&:hover': {
      boxShadow: theme.shadows[1],
      backgroundColor: theme.mixins.White(),
      '& h3': {
        color: theme.palette.common.SeaPort,
      },
      '& :not(.img-to-animate-wrapper) img, & .img-to-animate-wrapper': {
        transform: 'scale(1.1, 1.1)',
      },
      '& .icon img': {
        transform: 'none',
      },
      '& .MuiChip-root': {
        backgroundColor: theme.palette.background.default,
      },
    },
    '& .MuiChip-root': {
      cursor: 'pointer',
      ...theme.mixins.marginRight(theme.spacing(2)),
      backgroundColor: theme.palette.background.default,
      '&:last-child': {
        ...theme.mixins.marginRight('0'),
      },
      '& .MuiTypography-root': {
        color: theme.palette.common.SeaPort,
      },
    },
    '&.listing-card': {
      boxShadow: theme.shadows[4],
      backgroundColor: theme.mixins.White(),
      display: 'flex',
      flexDirection: 'column',
      height: '100%',
      '&:hover': {
        transition: 'box-shadow .3s',
        boxShadow: theme.shadows[1],
        '& .card-description': {
          boxShadow: 'none',
          backgroundColor: 'none',
        },
      },
      '& .card-description': {
        backgroundColor: 'transparent',
        boxShadow: theme.shadows[0],
        flex: 'auto',
        position: 'relative',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
        borderBottom: '0',
        '& h3': {
          minHeight: 'auto',
        },
      },
      '&.horizontal-card': {
        flexDirection: 'row',
        '& .card-description': {
          padding: `${theme.spacing(4)} ${theme.spacing(3)} ${theme.spacing(
            3
          )}`,
          ...theme.mixins.paddingLeft(pxToRem(20)),
          [theme.breakpoints.up('lg')]: {
            flex: '0 0 75%',
            maxWidth: '75%',
            margin: 0,
            padding: theme.spacing(3),
          },
          '& h3': {
            marginBottom: theme.spacing(1),
          },
          '& p': {
            color: theme.palette.text.darkGrey,
            marginBottom: theme.spacing(2),
            ...theme.palette.common.ellipsis(2),
            maxHeight:
              theme.typography.body2.lineHeight.replace('rem', '') * 2 + 'rem',
            [theme.breakpoints.up('lg')]: {
              ...theme.palette.common.ellipsis(3),
              maxHeight:
                theme.typography.body2.lineHeight.replace('rem', '') * 3 +
                'rem',
            },
          },
        },
        '& .img-wrapper': {
          flex: '0 0 25%',
          overflow: 'hidden',
          margin: `${theme.spacing(4)} ${theme.spacing(3)} ${theme.spacing(3)}`,
          ...theme.mixins.marginRight(0),
          [theme.breakpoints.up('lg')]: {
            margin: 0,
          },
          '& img': {
            display: 'block',
            height: 'auto',
          },
        },
      },
    },
  },
  rtlClass: {
    direction: theme.direction,
    height: '100%',
  },
  imageWrapper: {
    position: 'relative',
    overflow: 'hidden',
    // height: '216px',
    // [theme.breakpoints.up('md')]: {
    // height: '224px',
    // },
    '& picture': {
      paddingTop: '0',
    },
    '& :not(.img-to-animate-wrapper) img, & .img-to-animate-wrapper': {
      transition: 'all 1s ease',
    },
    '&.collage': {
      marginBottom: theme.spacing(4),
    },
  },
  cardContent: {
    display: 'flex',
    flexDirection: 'column',
    textAlign: theme.mixins.textLeft('left'),
    '& .MuiTypography-cardTitle1': {
      marginBottom: theme.spacing(1),
    },
    '& .bot-area': {
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'space-between',
      '& .cta-wrap': {
        marginTop: 'auto',
      },
    },
  },
  location: {
    display: 'flex',
    '&.MuiTypography-root': {
      color: theme.palette.text.tertiary,
      marginBottom: theme.spacing(2),
    },
  },
  duration: {
    display: 'flex',
    alignItems: 'center',
    '&.MuiTypography-root': {
      color: theme.palette.text.tertiary,
      marginBottom: theme.spacing(2),
    },
    '& .icon': {
      ...theme.mixins.marginRight(theme.spacing(1)),
      '& svg': {
        width: theme.spacing(3),
        height: theme.spacing(3),
      },
    },
  },
  highlights: {
    display: 'inline-flex',
    alignItems: 'center',
    '&.MuiTypography-root': {
      color: theme.mixins.Black(),
      marginBottom: theme.spacing(2),
      marginRight: theme.spacing(2),
    },
    '& .icon': {
      marginRight: theme.spacing(1),
      '& img': {
        width: pxToRem(20),
        height: pxToRem(20),
      },
    },
  },
  rating: {
    ...theme.typography.labelRegular,
    display: 'inline-flex',
    alignItems: 'center',
    paddingLeft: theme.spacing(1),
    '& svg': {
      width: theme.spacing(1.5),
      height: theme.spacing(1.5),
      marginRight: theme.spacing(0.5),
    },
  },
  viewsCounterTag: {
    position: 'absolute',
    top: pxToRem(16),
    ...theme.mixins.left(pxToRem(16)),
  },
  priceRangeTag: {
    position: 'absolute',
    bottom: pxToRem(16),
    ...theme.mixins.left(pxToRem(16)),
  },
  overViewSection: {
    paddingBottom: pxToRem(10.35),
    width: '100%',
    marginTop: pxToRem(12),
    overflow: 'hidden',
    '& .ratign-sec': {
      display: 'flex',
      alignItems: 'center',
    },
    '& .open-close-sec': {
      display: 'flex',
      marginTop: pxToRem(5),
    },
    [theme.breakpoints.up('lg')]: {
      paddingBottom: pxToRem(16),
    },
  },
  ratingImageWithLogo: {
    display: 'flex',
    '& .MuiSvgIcon-root': {
      width: theme.spacing(5),
      height: theme.spacing(5),
    },
  },
  totalReviews: {
    padding: '0',
    color: theme.palette.text.tertiary1,
  },
  ratingTripAdvisor: {
    display: 'inline-flex',
    background: theme.palette.background.transparent,
    ...theme.mixins.marginLeft(pxToRem(-5)),
    marginTop: pxToRem(-1),
  },
}));

export default useStyles;
