import { makeStyles } from '@material-ui/core/styles';
import { pxToRem } from '../../../../../styles/themes/vad/constants';
import {
  EVENT_TYPE,
  SIMILAR_SEARCH_EVENT_TAG_BACKGROUND_COLOR,
  SIMILAR_SEARCH_EVENT_TAG_COLOR,
} from '../../../../../constants/index';

const useEventStyles = makeStyles(theme => ({
  collageType1: {
    display: 'flex',
    flex: 'auto',
    borderColor: theme.mixins.Grey(),
    ...theme.mixins.paddingLeft(theme.spacing(1)),
    [theme.breakpoints.up('lg')]: {
      ...theme.mixins.paddingLeft(0),
      borderBottom: 'none',
    },
  },
  collageType1subHeading: {
    height: '90%',
    ...theme.mixins.borderRight(`1px solid ${theme.mixins.Grey(200)}`),
    backgroundColor: theme.mixins.White(),
    ...theme.mixins.marginRight(pxToRem(14)),
    [theme.breakpoints.up('lg')]: {
      ...theme.mixins.marginRight(0),
    },
  },
  cardDescription: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
    minWidth: '1px',
  },
  eventDateWrapper: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
    height: '100%',
  },
  eventDate: {
    color: theme.mixins.Black(),
  },
  eventMonth: {
    textTransform: 'uppercase',
  },
  collageType1Card: {
    color: theme.palette.text.tertiary,
    padding: `0 0 ${theme.spacing(2)}`,
    ...theme.mixins.paddingRight(theme.spacing(2)),
    [theme.breakpoints.up('lg')]: {
      padding: `0 ${theme.spacing(2)} ${theme.spacing(2)}`,
    },
  },
  CategoryTag: {
    textTransform: 'uppercase',
    color: ({ pageContentType }) =>
      pageContentType === EVENT_TYPE
        ? SIMILAR_SEARCH_EVENT_TAG_COLOR
        : theme.mixins.ADCalendar(),
    background: ({ pageContentType }) =>
      `${pageContentType === EVENT_TYPE
        ? SIMILAR_SEARCH_EVENT_TAG_BACKGROUND_COLOR
        : theme.mixins.ADCalendar(100)
      } !important`,
    marginRight: pxToRem(10),
    marginLeft: 0,
    '&:hover': {
      background: theme.mixins.ADCalendar(100),
    },
  },
  linkRoot: {
    display: 'flex',
    flexDirection: 'column',
    height: '100%',
    backgroundColor: theme.mixins.White(),
    borderBottom: ({ umbrellaECount }) =>
      umbrellaECount && `4px solid ${theme.mixins.ADCalendar()}`,
    '& .imageWrap': {
      '& img': {
        height: '100%',
      },
    },
    '&:hover': {
      backgroundColor: theme.mixins.White(),
      '& h3': {
        color: theme.palette.common.SeaPort,
      },
      '& :not(.img-to-animate-wrapper) img, & .img-to-animate-wrapper': {
        transform: 'scale(1.1, 1.1)',
      },
      '& .icon img': {
        transform: 'none',
      },
    },
  },
  categoryOnImageWrapper: {
    display: 'flex',
    position: 'absolute',
    top: theme.spacing(2),
    left: theme.spacing(3),
  },
  categoryOnImage: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    padding: `${pxToRem(4)} ${theme.spacing(1)}`,
    borderRadius: pxToRem(2),
    paddingBottom: pxToRem(3),
    color: theme.mixins.White(),
    backgroundColor: theme.mixins.ADCalendar(),
    textTransform: 'uppercase',
  },
  umbrellaEventCountWrapper: {
    position: 'absolute',
    bottom: theme.spacing(3),
    left: theme.spacing(3),
  },
  umbrellaEventCount: {
    display: 'flex',
    justifyContent: 'center',
    flexDirection: 'column',
    backgroundColor: theme.mixins.ADCalendar(),
    height: pxToRem(65),
    width: pxToRem(65),
    '& p': {
      color: theme.mixins.milkWhite(),
    },
    '& span': {
      color: theme.mixins.White(),
    },
  },
  locationItem: {
    color: theme.palette.text.tertiary,
    paddingBottom: theme.spacing(2),
    [theme.breakpoints.up('lg')]: {
      paddingTop: 0,
    },
    '& .location-icon': {
      color: theme.mixins.ADCalendar(),
      border: `2px solid ${theme.mixins.ADCalendar()}`,
      backgroundColor: theme.mixins.White(),
      width: pxToRem(35),
      height: pxToRem(35),
      '& svg': {
        width: pxToRem(21),
        height: pxToRem(16),
      },
    },
    '& .favourite-icon': {
      border: 'none',
      marginRight: 0,
      color: theme.mixins.Black(),
    },
  },
  timings: {
    marginBottom: pxToRem(10),
  },
}));

export default useEventStyles;
